/* Dark mode background */
.canvas {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #1e1e1e; /* Dark background for the canvas */
  user-select: none;
}

/* Debug coordinates */
.debug-coordinates {
  position: absolute;
  top: 10px;
  left: 10px;
  color: white; /* White text for better contrast in dark mode */
  font-size: 16px;
  font-family: Arial, sans-serif;
  z-index: 10; /* Ensure it's always on top */
}

/* Grid background with light gray lines */
.grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #d3d3d3 1px, transparent 1px),
              linear-gradient(#d3d3d3 1px, transparent 1px);
  background-size: 50px 50px;
  z-index: 1;
  transition: background-position 0.1s linear; /* Smooth transition for grid movement */
}

/* Timeline container */
.timeline {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 2px;
  z-index: 2; /* Ensure the timeline is above the grid */
}

/* Future line */
.line.future-line {
  position: absolute;
  top: -1px;
  height: 2px;
  width: 100%;
  background-color: #d3d3d3; /* Light gray for future line */
  opacity: 0.6; /* Slight transparency for a subtle look */
}

/* Past line */
.line.past-line {
  position: absolute;
  top: 1px;
  height: 2px;
  width: 100%;
  background-color: #00bcd4; /* Cyan for past line */
  opacity: 0.6; /* Slight transparency for a subtle look */
}
